nav {
    background-color: #f2f2f2;
    padding: 10px;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  li {
    display: inline-block;
    margin-right: 10px;
  }
  
  a {
    color: #333;
    text-decoration: none;
    padding: 5px;
  }
  
  a:hover {
    background-color: #333;
    color: #fff;
  }
  @media (max-width: 500px) {
    ul {
      margin-bottom: 1rem;
    }
  }

 